export default {
	data() {
		return {
			isOpen:true,
			bgMp3Url:"",
			number:1,
			time:''
		}
	},
	onLoad(option) {
		
		this.$store.commit('platform')
		const i = this.$store.state.platform
		this.$store.commit('stop')
			
		
		// // 测试
		// option.num = 6
		
		// 使手机振动15ms
		wx.vibrateShort()
		uni.stopAccelerometer()
		
		// 接受变量参数
		if(option.num){
			this.$store.commit('setNum',option.num)
		}else{
			this.$store.commit('randomNum')
		}
		this.number = this.$store.state.num
		

		this.$store.commit('play',{
			url:'/static/27.mp3',
			loop:true	
		})		
		
		clearTimeout(this.time)
		this.time = setTimeout(() => {
			this.openClick()
		},9000)
		
	},
	
	onShow() {
		
	},
	methods: {
		openClick(){
			clearTimeout(this.time)
			this.$store.commit('stop')
			if(this.number <= 5){
				uni.navigateTo({
					url:"../detail/detail",
				})
			}
			else{
				uni.navigateTo({
					url:"../shake/shake",
				})
			}
			
		},
		refuse(){
			this.isOpen = this.isOpen
		},
		mytouchmove(){
			// 使手机振动15ms
			wx.vibrateShort()
		}
	},
	onUnload() {
		this.$store.commit('stop')
		uni.clearStorage()
	}
	
}
